  cursor: pointer;
}
body section.footer .footer-social-media {
  display: flex;
  gap: 2.5rem;
  margin-top: 4.5rem;
  color: #286758;
}

@media screen and (min-width: 1440px) {
  body header {
    background-image: url(/images/desktop/image-header.jpg);
    background-position-y: 50%;
    background-repeat: repeat-x;
    background-size: 80%;
    background-position-x: 40%;
  }
  body header .container {
    padding: 3.5rem 4.5rem 4rem 3.5rem;
  }
  body header .container nav button {
    display: none;
  }
  body header .container nav ul {
    all: unset;
    display: flex;
    gap: 4rem;
    font-size: 1.4rem;
    list-style: none;
    color: white;
  }
  body header .container nav ul li:nth-child(4) {
    background-color: white;
    padding: 1.3rem 2.2rem;
    transform: translateY(-24%);
    font-size: 1.2rem;
  }
  body header .container nav ul li:nth-child(4):hover {
    background-color: #7aceff;
    color: white;
  }
  body header .container h1 {
    margin-bottom: 37rem;
    font-size: 4.5rem;
  }
  body header .container img.arrow {
    top: 30%;
    right: 50%;
    transform: scale(80%);
  }
  body section.transform {
    flex-direction: row-reverse;
  }
  body section.transform img {
    flex: 1;
  }
  body section.transform .transform-text {
    flex: 1;
  }
  body section.transform .transform-text .transform-text-wrapper {
    padding: 15rem 9rem 7rem 17rem;
    text-align: left;
  }
  body section.transform .transform-text .transform-text-wrapper h1 {
    font-size: 4.2rem;
  }
  body section.transform .transform-text .transform-text-wrapper h4 {
    margin-left: 1rem;
  }
  body section.transform .transform-text .transform-text-wrapper h4:after {
    margin: -0.5rem -0.7rem;
    width: 28%;
    opacity: 1;
  }
  body section.stand-out {
    flex-direction: row;
  }
  body section.stand-out img {
    flex: 1;
  }
  body section.stand-out .stand-out-text {
    flex: 1;
  }
  body section.stand-out .stand-out-text .stand-out-text-wrapper {
    padding: 15rem 17rem 7rem 9rem;
    text-align: left;
  }
  body section.stand-out .stand-out-text .stand-out-text-wrapper h1 {
    font-size: 4.2rem;
  }
  body section.stand-out .stand-out-text .stand-out-text-wrapper h4 {
    margin-left: 1rem;
  }
  body section.stand-out .stand-out-text .stand-out-text-wrapper h4:after {
    margin: -0.5rem -0.7rem;
    width: 28%;
    opacity: 1;
  }
  body .container-design-photography {
    flex-direction: row;
  }
  body .container-design-photography section.graphic-design {
    flex: 1;
    padding: 38.5rem 17rem 5.5rem 17rem;
    background-image: url(/images/desktop/image-graphic-design.jpg);
  }
  body .container-design-photography section.photography {
    flex: 1;
    padding: 38.5rem 17rem 5.5rem 17rem;
    background-image: url(/images/desktop/image-photography.jpg);
  }
  body section.testimonials {
    padding: 16rem 16.8rem 7.8rem 17.5rem;
  }
  body section.testimonials h4.heading {
    font-size: 2rem;
    letter-spacing: 0.5rem;
  }
  body section.testimonials .container-testimonials {
    flex-direction: row;
    margin-top: 8rem;
  }
  body section.testimonials .container-testimonials .testimonial {
    gap: 5.5rem;
  }
  body section.testimonials .container-testimonials .testimonial img {
    width: 7.5rem;
    height: 7.5rem;
  }
  body section.testimonials .container-testimonials .testimonial .testimonial-info {
    margin-top: 2rem;
  }
  body section.gallery {
    flex-direction: row;
  }
  body section.gallery img:nth-child(1), body section.gallery img:nth-child(2), body section.gallery img:nth-child(3), body section.gallery img:nth-child(4) {
    display: none;
  }
  body section.gallery img:nth-child(5), body section.gallery img:nth-child(6), body section.gallery img:nth-child(7), body section.gallery img:nth-child(8) {
    display: block;
  }
  body section.gallery img {
    width: 36rem;
    height: 44.7rem;
  }
  body section.footer .footer-info p:hover {
    color: white;
  }
  body section.footer .footer-social-media a:hover {
    color: white;
  }
}